/*
	App routes files
*/
import React, { Suspense, lazy } from 'react';
import {
	Route,
	Switch,
	BrowserRouter as Router
} from 'react-router-dom'





// lazy load components-pages
const AllComps = React.lazy(() => import('./comps/allCamps'));
const Tos = React.lazy(() => import('./comps/tos/tos'));


// auth routes


// Posts
// import Posts from '../Posts/Posts/Posts';

/*
 * Defing client route
 * 
*/
 const Routes = () => {
	return (
		<Router>
			<Suspense fallback={<div>Loading...</div>}>
				<Switch>
					<Route path="/tos" component={Tos} />
					<Route path="/" component={AllComps} />
				</Switch>
			</Suspense>
		</Router>

	)
}


export default Routes;